import React, { useState, useEffect } from 'react';

const FetchSessionData = ({ sessionId, onSessionData }) => {
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    if (!sessionId) {
      setError('Session ID is undefined. Unable to fetch session data.');
      setLoading(false);
      return;
    }

    const fetchData = async () => {
      try {
        console.log(`Fetching session data for sessionId: ${sessionId}...`);
        const response = await fetch(`http://127.0.0.1:8080/api/session/${sessionId}/`);
        if (!response.ok) {
          throw new Error(`Failed to fetch session data: ${response.status}`);
        }
        const data = await response.json();
        console.log('Fetched session data:', data);
        onSessionData(data); // Pass data to the parent component
      } catch (err) {
        console.error('Error fetching session data:', err.message);
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, [sessionId, onSessionData]);

  if (loading) {
    return <p>Loading session data...</p>;
  }

  if (error) {
    return <p style={{ color: 'red' }}>Error: {error}</p>;
  }

  return null; // No UI since data is passed to the parent component
};

export default FetchSessionData;
